import {Component, OnInit, OnDestroy, Input} from '@angular/core';
import {ProductService} from '../products/product.service';
import {Product} from '../common/models/product'; 
import {ServiceCategoryService} from '../service-category/service-category.service';
import {ServiceCategory} from '../common/models/service-category';
import {Observable, Subscription} from 'rxjs/Rx'; 
import {Globals} from '../globals';

@Component({
    selector: 'app-home',
    templateUrl: './home.component.html',
    styleUrls: ['./home.component.css']
})
export class HomeComponent  implements OnInit, OnDestroy {

    serviceCategories: ServiceCategory[];
    products: Product[];

    timerSubscription: Subscription;
    serviceCategoryDisplayId: number;
    iNowDisplay: number;
    maxSize: number;

    constructor(private _productService: ProductService,
        private _categoryService: ServiceCategoryService,
        private globals:Globals) { 
        //        this.state = 'inactive'
        //        this.heroes = [new Hero("Test1", "active"), new Hero("Test2", "inactive"), new Hero("Test3", "inactive")];
    }

    ngOnInit() {
        this.getServiceCategories();
        this.getProducts();

        let timer = Observable.timer(2000, 15000);
        this.timerSubscription =
            timer.subscribe(t => {
                this.changeDisplayedServiceCategory(this.maxSize);
            });
    }

    ngOnDestroy() {
        this.timerSubscription.unsubscribe();
    }

    getProducts() {
        //this.productsSubscription = 
        this._productService.getAll()
            .subscribe(prods => this.products = prods)
            ;
    }

    getServiceCategories(): any {
        //this.serviceCategoriesSubscription = 
        this._categoryService.getAll()
            .subscribe(catgs => {
                this.serviceCategories = catgs;
                this.maxSize = this.serviceCategories.length;
                if (this.maxSize >= 1) {
                    let serviceCategory = this.serviceCategories[0];
                    this.serviceCategoryDisplayId = serviceCategory.id;
                }
            })
            ;
    }
    
    getImageUrl(filename:String ) : String{
        console.log (this.globals.hostUrl + '/storage/view/' +  filename ) ;
       return this.globals.hostUrl + '/storage/view/' +  filename ;
    }

    changeDisplayedServiceCategory(iMax: number) {
        if (iMax >= 1) {
            this.iNowDisplay += 1;

            if (this.iNowDisplay >= iMax || !(this.iNowDisplay)) {
                this.iNowDisplay = 0;
            }

            let serviceCategory = this.serviceCategories[this.iNowDisplay];
            this.serviceCategoryDisplayId = serviceCategory.id;
            //console.log("iNowDisplay : " + this.iNowDisplay);
        }
    }

    //    toggleState() {
    //        this.state = this.state === 'active' ? 'inactive' : 'active';
    //    }
}

