import {Component, OnInit, AfterViewInit} from '@angular/core';
import {ScriptService} from '../common/services/script.service';
import {ServiceCategory} from '../common/models/service-category';
import {ServiceCategoryService} from '../service-category/service-category.service'; 
import {Globals} from '../globals';

@Component({
    selector: 'app-home-r',
    templateUrl: './home-r.component.html',
    styleUrls: ['./home-r.component.css'],
    providers: [ScriptService]
})
export class HomeRComponent implements OnInit, AfterViewInit { 

    constructor(private scriptService: ScriptService, public globals:Globals) { 
    }

    ngOnInit() { }

    ngAfterViewInit(): void {

        this.scriptService.loadScript("easing-js");
        this.scriptService.loadScript("wow-js");
        //this.scriptService.loadScript("google-api");

        this.scriptService.loadScript("waypoints-js");
        this.scriptService.loadScript("counterup-js");
        this.scriptService.loadScript("hoverIntent-js");
        this.scriptService.loadScript("superfish-js");

        this.scriptService.loadScript("main-del");
    } 

}



